.obj-width {
  max-width: 1200px;
  margin: auto;
}

header {
  z-index: 1000;
  background: #3d8d64 center / cover;
  position: sticky;
  top: 0;
  box-shadow: 0 7px 10px #0000004d;
}

.navbar {
  justify-content: space-between;
  align-items: center;
  padding: 1.3rem 0;
  display: flex;
}

.navbar .date p {
  color: #3d8d64;
  background: #333;
  border-radius: 5px;
  font-weight: 700;
}

select {
  width: 100px;
}

.navbar .business-cont select {
  color: #333;
  background: none;
  border: none;
  border-radius: 10px;
  font-size: 1em;
}

.navbar .business-cont select optgroup {
  color: #333;
  background: #3d8d64;
}

.navbar .logo {
  color: #3d8d64;
  letter-spacing: 2px;
  background: #333;
  border-radius: 0 10px;
  margin-left: 10px;
  font-family: Poppins, sans-serif;
  font-size: 1.5rem;
  text-decoration: none;
}

.navbar .new {
  color: #3b22b6;
}

.navbar .york {
  color: #fff;
}

.navbar .times {
  color: #e0743e;
}

.news h1 {
  color: #3d8d64;
  justify-content: center;
  margin-top: 40px;
  display: flex;
}

#bg-container {
  flex-wrap: wrap;
  justify-content: flex-start;
  align-items: stretch;
  padding: 3rem 0;
  display: flex;
}

.blog-card {
  cursor: pointer;
  border: 2px solid #3d8d64;
  border-radius: 10px;
  width: 280px;
  margin: 10px;
  padding: 1rem;
  font-family: Poppins, sans-serif;
}

.blog-card:hover {
  box-shadow: 4px 4px 10px #89f3a7;
}

.blog-card img {
  object-fit: cover;
  border-radius: 5px;
  width: 100%;
  height: 280px;
  margin-bottom: 1rem;
}

.blog-card h2 {
  color: #3d8d64;
  padding: 10px;
  font-size: 1.7rem;
  font-weight: 500;
}

.blog-card p {
  color: #fff;
  padding: 10px;
  font-size: 1rem;
  font-weight: 300;
}

@media (width <= 536px) {
  header {
    padding: 0 5px;
    position: sticky;
    top: 0;
  }

  .navbar .business-cont select {
    color: #333;
    background-color: #3d8d64;
    border: none;
    font-size: .3em;
  }

  .navbar .business-cont select optgroup {
    color: #333;
    background-color: #3d8d64;
  }

  .navbar .date p {
    color: #333;
    background: #3d8d64;
    border-radius: 5px;
    font-size: .5em;
    font-weight: 400;
  }

  select {
    width: 50px;
  }

  .navbar .logo {
    border-radius: 0 5px;
    font-size: .8rem;
  }

  .blog-card {
    width: 100%;
  }

  .blog-card img {
    object-fit: cover;
    border-radius: 5px;
    width: 100%;
    margin-bottom: 1rem;
  }

  .blog-card h2 {
    color: #3d8d64;
    font-size: .7rem;
    font-weight: 200;
  }

  .blog-card p {
    font-size: .5rem;
    font-weight: 100;
  }
}

.footer-icon {
  text-align: center;
  justify-content: center;
}

img {
  width: 40px;
  height: 40px;
}

p {
  color: #fff;
}
